/* eslint-disable no-console */
import io from "socket.io-client";
import socketEvents from "./events";
import emitStates from "./emit";

let socket;

const initSockets = ({ setRoom, widgetState, apiKey }, options) => {
  fetch(`${process.env.VCLERK_ENDPOINT}/guest/session`, {
    credentials: "include",
    headers: {
      "X-VideoClerk-API-Key": apiKey,
    },
  }).then((res) => {
    res.json().then((session) => {
      const socketOptions = options;
      socketOptions.query = {
        ...socketOptions.query,
        session_id: session.session_id,
      };

      socket = io(`${process.env.VCLERK_ENDPOINT}`, {
        transports: ["websocket"],
        ...socketOptions,
      });

      const events = socketEvents({ setRoom, widgetState });

      for (let i = 0; i < events.length; i += 1) {
        socket.on(events[i].eventName, events[i].action);
      }
      return socket;
    });
  });
};

const emit = (emitName, data) => {
  if (!Object.keys(emitStates).includes(emitName)) {
    console.error(`${emitName} is not a proper emit name.`);
    return;
  }
  if (socket) {
    socket.emit(emitStates[emitName], data);
  }
};

const disconnectSocket = () => socket && socket.close();

export { disconnectSocket, initSockets, emit };
