import React, { useState } from "react";
import PropTypes from "prop-types";
import WidgetProvider from "context/widgetContext";
import Conversation from "src/screens/Conversation";
import Intro from "screens/Intro";
import VClerkErrorBoundary from "screens/ErrorBoundary";
import "./index.scss";
import SkylinkProvider from "./context/conversationContext";

const ProductWidget = ({
  onProductAdd,
  initialProducts,
  initialCart,
  hideBackground,
  apiKey,
  queue,
  onClose,
  skylink_key,
  onGoToCart,
}) => {
  const [startedChat, setStartedChat] = useState(undefined);

  return (
    <>
      {hideBackground && <div className="vclerk-pw-overlay">&nbsp;</div>}
      <VClerkErrorBoundary onClose={onClose}>
        <WidgetProvider
          queue={queue}
          apiKey={apiKey}
          onClose={onClose}
          skylink_key={skylink_key}
        >
          <div
            className={`vclerk-pw-wrapper ${startedChat ? "vclerk-pw-sc" : ""}`}
          >
            {!startedChat && (
              <Intro onConfirm={() => setStartedChat(true)} onClose={onClose} />
            )}
            {startedChat && (
              <SkylinkProvider
                onProductAdd={onProductAdd}
                onGoToCart={onGoToCart}
                initialProducts={initialProducts}
                initialCart={initialCart}
              >
                <Conversation onClose={onClose} />
              </SkylinkProvider>
            )}
          </div>
        </WidgetProvider>
      </VClerkErrorBoundary>
    </>
  );
};

ProductWidget.defaultProps = {
  onProductAdd: null,
  queue: "",
  hideBackground: true,
  initialProducts: [],
  initialCart: [],
  onGoToCart: null,
};

ProductWidget.propTypes = {
  apiKey: PropTypes.string.isRequired,
  onProductAdd: PropTypes.func,
  initialProducts: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.string),
    PropTypes.func,
  ]),
  initialCart: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.string),
    PropTypes.func,
  ]),
  onGoToCart: PropTypes.func,
  hideBackground: PropTypes.bool,
  queue: PropTypes.string,
  onClose: PropTypes.func.isRequired,
  skylink_key: PropTypes.string.isRequired,
};

export default ProductWidget;
