/* eslint-disable jsx-a11y/media-has-caption */
import React, { useEffect, useContext, useState } from "react";
import I18n from "i18n";
import { WidgetContext } from "context/widgetContext";
import states from "utils/states";
import Spinner from "src/components/Spinner";
import VideoControllers from "src/components/VideoControllers";
import useEventListener from "hooks/useEventListener";
import "./index.scss";
import FullSize from "assets/icons/fullSize";
import { ConversationContext } from "src/context/conversationContext";

const PeerVideo = () => {
  const { widgetState, setWidgetState, room } = useContext(WidgetContext);
  const [peerLoading, setPeerLoading] = useState(true);
  const [fullSizeVideo, setFullSizeVideo] = useState(false);
  const { peerStreams } = useContext(ConversationContext);

  const [peerName, setPeerName] = useState();

  useEffect(() => {
    if (peerStreams.audio) {
      const peerAudio = document.getElementById("peerAudio");
      window.attachMediaStream(peerAudio, peerStreams.audio.stream);
    }

    if (peerStreams.video) {
      const peerVideo = document.getElementById("peerVideo");
      window.attachMediaStream(peerVideo, peerStreams.video.stream);
      setPeerLoading(false);
      const { peerInfo } = peerStreams.video;
      setPeerName(peerInfo?.userData?.displayName);
      setWidgetState(states.connected);
      setPeerLoading(false);
    }
  }, [peerStreams]);

  const toggleVideoFullSize = () => {
    const videoContainer = document.getElementById("vclerk-videos-container");
    if (videoContainer) {
      if (!fullSizeVideo) {
        videoContainer.classList.add("vclerk-video-fullSize");
      } else {
        videoContainer.classList.remove("vclerk-video-fullSize");
      }
      setFullSizeVideo(!fullSizeVideo);
    }
  };

  const toggleEscapeOnFullSize = (e) => {
    if (
      e.key === "Escape" &&
      document
        .getElementById("vclerk-videos-container")
        .classList.contains("vclerk-video-fullSize")
    ) {
      toggleVideoFullSize();
    }
  };

  useEventListener("keyup", toggleEscapeOnFullSize, window);

  return (
    <div className="vclerk-pv-wrapper">
      <div
        className={`vclerk-pv-spinner ${
          peerLoading ? "vclerk-show" : "vclerk-hide"
        }`}
      >
        <Spinner />
      </div>
      <div
        className={`vclerk-pv-container ${
          peerLoading ? "vclerk-hide" : "vclerk-show"
        }`}
      >
        <div className="vcler-pv-v-c">
          <video className="vclerk-pv" id="peerVideo" autoPlay playsInline />
          <div className="vclerk-pv-controllers">
            <div
              tabIndex={0}
              role="button"
              className="vclerk-pv-c-fs"
              onClick={() => {
                toggleVideoFullSize();
              }}
            >
              <FullSize
                width={20}
                height={20}
                color={fullSizeVideo ? "#beaeff" : "#fff"}
              />
            </div>
          </div>
          <VideoControllers room={room} self={false} clickable={false} />
        </div>
      </div>
      <div className="vclerk-pv-text">
        {peerName && `${peerName} ${I18n.t("in_call.is_helping_you")}`}
      </div>
      <audio id="peerAudio" autoPlay muted={widgetState === states.outro} />
    </div>
  );
};

export default React.memo(PeerVideo);
