import React, { useContext } from "react";
import PropTypes from "prop-types";
import Button from "src/components/Button";
import VClerkLogo from "assets/icons/VClerkLogo";
import Rating from "src/components/Rating";
import I18n from "i18n";
import { WidgetContext } from "context/widgetContext";
import { ConversationContext } from "src/context/conversationContext";

const Outro = ({ onClose }) => {
  const { room, apiKey } = useContext(WidgetContext);
  const { onGoToCart } = useContext(ConversationContext);

  const submitRating = (rating) => {
    fetch(`${process.env.VCLERK_ENDPOINT}/users/rating`, {
      method: "POST",
      headers: {
        "X-VideoClerk-API-Key": apiKey,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        clerk_id: room.clerk_id,
        queue_id: room.queue_id,
        guest_id: room.guest_id,
        rating,
      }),
    });
  };

  const handleOnGoToCart = () => {
    onGoToCart();
    onClose();
  };

  return (
    <div className="vclerk-end-wrapper">
      <div className="vclerk-end-logo">
        <VClerkLogo width={160} height={72} color="#080a1e" />
      </div>
      <div className="vclerk-end-descr">
        <div className="vclerk-end-title">{I18n.t("outro.heading")}</div>
      </div>
      <div className="vclerk-end-rating">
        <Rating setRating={(rate) => submitRating(rate)} />
      </div>
      <div className="vclerk-end-actions">
        <div>
          <Button onClick={onClose}>{I18n.t("outro.continue_shopping")}</Button>
        </div>
        {onGoToCart && (
          <div>
            <Button onClick={handleOnGoToCart}>
              {I18n.t("outro.go_to_cart")}
            </Button>
          </div>
        )}
      </div>
    </div>
  );
};

Outro.propTypes = {
  onClose: PropTypes.func.isRequired,
};

export default Outro;
