import React, { useState, useEffect, useContext } from "react";
import { ConversationContext } from "context/conversationContext";
import I18n from "i18n";
import ImageRotator from "src/components/ImageRotator";
import Button from "src/components/Button";
import "./index.scss";
import Spinner from "../Spinner";

const getActiveProduct = (prods, aProdID) =>
  prods.find((p) => p.id === aProdID);

const Product = () => {
  const {
    products,
    activeProduct,
    handleProductAddedToCart,
    productsAddedToCartIds,
  } = useContext(ConversationContext);
  const [addToCartLoading, setAddToCartLoading] = useState(false);

  if (!activeProduct || !activeProduct.id) return null;

  const onAddToCart = () => {
    setAddToCartLoading(true);
    handleProductAddedToCart(
      getActiveProduct(products, activeProduct.id)
    ).finally(() => {
      setAddToCartLoading(false);
    });
  };

  const [state, setState] = useState({
    activeProduct: getActiveProduct(products, activeProduct.id),
  });

  useEffect(() => {
    if (activeProduct && activeProduct.id) {
      const prod = getActiveProduct(products, activeProduct.id);
      setState({
        activeProduct: prod,
      });
    }
  }, [activeProduct]);

  const { activeProduct: product } = state;

  return (
    <div className="vclerk-p-wrapper">
      {!!product && (
        <>
          <div className="vclerk-p-image-wrapper">
            <ImageRotator
              items={
                product.additional_image_links?.length
                  ? product.additional_image_links
                  : [
                      {
                        kind: "image",
                        url: product.image_link_cdn || product.image_link,
                      },
                    ]
              }
              productId={product.id}
              mediaIndex={activeProduct.mediaIndex}
            />
          </div>
        </>
      )}
      <div className="vclerk-p-action-container">
        <div className="vclerk-p-action-descr">
          <div className="vclerk-p-action-descr-name">{product.title}</div>
          <div className="vclerk-p-action-descr-price">
            {`${product.sale_price ?? product.price} ${product.currency}`}
          </div>
        </div>
        {handleProductAddedToCart && (
          <>
            {addToCartLoading ? (
              <Spinner />
            ) : (
              <>
                {!productsAddedToCartIds.includes(product.external_id) ? (
                  <Button onClick={() => onAddToCart(handleProductAddedToCart)}>
                    {I18n.t("in_call.add_to_cart")}
                  </Button>
                ) : (
                  <>
                    <div className="vclerk-p-action-item-in-cart">
                      {I18n.t("in_call.item_in_cart")}
                    </div>
                  </>
                )}
              </>
            )}
          </>
        )}
      </div>
      {!product && (
        <div className="wizard-no-selected-product">
          {I18n.t("in_call.choose_product")}
        </div>
      )}
    </div>
  );
};

export default React.memo(Product);
