import React, { useContext } from "react";
import PropTypes from "prop-types";
import Spinner from "src/components/Spinner";
import VClerkLogo from "assets/icons/VClerkLogo";
import Close from "assets/icons/close";
import SelfVideo from "src/components/SelfVideo";
import I18n from "i18n";
import states from "utils/states";
import { WidgetContext } from "context/widgetContext";

const Queue = ({ onClose }) => {
  const { room, widgetState } = useContext(WidgetContext);

  return (
    <div className="vclerk-wl-container">
      <div className="vclerk-cv-wrapper-logo">
        <VClerkLogo color="#f4f3f2" width={56} height={16} />
      </div>
      <div
        role="button"
        className="vclerk-wl-close"
        onClick={onClose}
        tabIndex={0}
      >
        <Close color="#080a1e" width={20} height={20} />
      </div>
      <div className="vclerk-inner-container">
        <div className="vclerk-wl-video-container">
          <SelfVideo
            iconSizes={{
              width: 24,
              height: 24,
            }}
            style={{ width: 240, height: 200 }}
          />
        </div>
        {widgetState === states.connecting ? (
          <>
            <div className="vclerk-wl-standby">{I18n.t("queue.stand_by")}</div>
            <div className="vclerk-wl-sb">
              {I18n.t("queue.connecting_to_clerk")}
            </div>
            <div className="vlcerk-wl-spinner">
              <Spinner type="dark" height={30} width={30} />
            </div>
          </>
        ) : (
          <>
            <div className="vclerk-wl-content">
              <div className="vclerk-wl-cp-title">
                {I18n.t("queue.position_in_queue")}
              </div>
              <div className="vclerk-wl-cp-count">{room?.position ?? "--"}</div>
            </div>
            <div className="vclerk-wl-content">
              <div className="vclerk-wl-cp-title">
                {I18n.t("queue.estimated_time")}
              </div>
              <div className="vclerk-wl-cp-count">
                {`${
                  room?.estimated_wait_time
                    ? Math.ceil(room?.estimated_wait_time / 60000)
                    : "-- "
                }m`}
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

Queue.propTypes = {
  onClose: PropTypes.func.isRequired,
};

export default Queue;
