import React, { useContext, useEffect } from "react";
import PropTypes from "prop-types";
import I18n from "i18n";
import { WidgetContext } from "context/widgetContext";
import states from "utils/states";
import VideoControllers from "src/components/VideoControllers";
import Hangup from "assets/icons/Hangup";
import { ConversationContext } from "src/context/conversationContext";
import "./index.scss";

const SelfVideo = ({
  style,
  wrapperStyle,
  isFromCustomer,
  iconSizes,
  iconTextSize,
  onClose,
}) => {
  const { room, widgetState } = useContext(WidgetContext);
  const { selfStreams } = useContext(ConversationContext);

  useEffect(() => {
    if (selfStreams && selfStreams[1]) {
      const selfVideo = document.getElementById("selfVideo");
      window.attachMediaStream(selfVideo, selfStreams[1]);
    }
  }, [selfStreams]);

  let statusText;
  switch (widgetState) {
    case states.init:
      statusText = I18n.t("queue.connecting");
      break;
    case states.mediaRejected:
      statusText = I18n.t("queue.media_rejected");
      break;
    case states.mediaAccessed:
    case states.connecting:
      statusText = null;
      break;
    default:
      break;
  }

  return (
    <div
      className={`vclerk-sv-wrapper ${isFromCustomer ? "vclerk-fc" : ""}`}
      style={wrapperStyle}
    >
      <div
        className={`vclerk-sv-container ${
          isFromCustomer ? "vclerk-sv-fc" : ""
        }`}
        style={style}
      >
        {statusText && (
          <div className="vclerk-sv-status-text">{statusText}</div>
        )}
        <video
          className="vclerk-sv"
          id="selfVideo"
          autoPlay
          muted
          playsInline
        />
      </div>
      <div className="vclerk-sv-info-container">
        <VideoControllers
          room={room}
          iconSizes={iconSizes}
          iconTextSize={iconTextSize}
        />
        {!statusText && isFromCustomer && (
          <div className="vclerk-sv-ec-container">
            <div
              className="vclerk-sv-ec-button"
              role="button"
              tabIndex={0}
              onClick={onClose}
            >
              <Hangup width={22} height={22} />
              {I18n.t("in_call.hangup")}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

const shouldUpdate = (prevState, nextState) => prevState.id !== nextState.id;

SelfVideo.defaultProps = {
  style: {},
  wrapperStyle: {},
  isFromCustomer: false,
  iconSizes: {
    width: 24,
    height: 24,
  },
  iconTextSize: 14,
  onClose: () => {},
};

SelfVideo.propTypes = {
  style: PropTypes.shape({}),
  wrapperStyle: PropTypes.shape({}),
  isFromCustomer: PropTypes.bool,
  iconSizes: PropTypes.shape({
    width: PropTypes.number,
    height: PropTypes.number,
  }),
  onClose: PropTypes.func,
  iconTextSize: PropTypes.number,
};

export default React.memo(SelfVideo, shouldUpdate);
