import React from "react";
import PropTypes from "prop-types";
import "./index.scss";

const types = {
  BASE: "base",
  DARK: "dark",
};

const Spinner = ({ width, height, type }) => {
  switch (type) {
    case types.DARK:
      return <div className="loader-dark" style={{ width, height }} />;
    case types.BASE:
    default:
      return <div className="loader" style={{ width, height }} />;
  }
};

Spinner.defaultProps = {
  width: 24,
  height: 24,
  type: types.BASE,
};

Spinner.propTypes = {
  width: PropTypes.number,
  height: PropTypes.number,
  type: PropTypes.oneOf([types.BASE, types.DARK]),
};

export default Spinner;
