import React from "react";
import PropTypes from "prop-types";
import "./index.scss";

const Dropdown = ({ onChange, elements }) => {
  let titles = elements.map((el) => el.title);
  titles = titles.filter((item, index) => titles.indexOf(item) === index); // dedup

  const getElementsForTitle = (title) =>
    elements.filter((el) => el.title === title);

  return (
    <div className="vclerk-dropdown">
      {titles.map((title, index) => (
        <div key={title} className="vclerk-dropdown-section">
          <div className="vclerk-dropdown-section-title">{title}</div>
          <form>
            {getElementsForTitle(title).map((el) => (
              // eslint-disable-next-line jsx-a11y/label-has-associated-control, jsx-a11y/no-noninteractive-element-interactions
              <label
                className="vclerk-dropdown-radio"
                key={`${el.kind}-${el.id}`}
                onClick={(e) => e.stopPropagation()} // https://stackoverflow.com/a/47006961
              >
                <span className="vclerk-dropdown-name">{el.label}</span>
                <input
                  type="radio"
                  checked={el.isSelected}
                  name={title}
                  onChange={() => onChange(el)}
                />
                <span className="vclerk-dropdown-radio-checkmark" />
              </label>
            ))}
          </form>
          {index < titles.length - 1 && <hr />}
        </div>
      ))}
    </div>
  );
};

Dropdown.defaultProps = {};

Dropdown.propTypes = {
  elements: PropTypes.arrayOf(
    PropTypes.shape({
      kind: PropTypes.string.isRequired,
      id: PropTypes.string.isRequired,
      label: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      isSelected: PropTypes.bool.isRequired,
    })
  ).isRequired,
  onChange: PropTypes.func.isRequired,
};

export default Dropdown;
