import React, { useContext, useEffect, useState } from "react";
import PropTypes from "prop-types";
import useClickOut from "hooks/useClickOut";
import Dropdown from "src/components/Dropdown";
import { WidgetContext } from "context/widgetContext";
import "./index.scss";

const DropdownDevices = ({ open, onClickOut }) => {
  const { mediaDevices, setCurrentMediaDevices } = useContext(WidgetContext);
  const [position, setPosition] = useState("below");
  const { ref } = useClickOut(onClickOut);

  useEffect(() => {
    if (open) {
      const currentItem = ref.current;
      const currentItemsBounds = currentItem.getBoundingClientRect();
      const dropdownContent = currentItem.children[0];
      // In case the dropdown`s content is too large for the viewport on bottom
      if (
        currentItemsBounds.top + dropdownContent.clientHeight >
        window.innerHeight
      ) {
        setPosition("top");
      }
    }
  }, [open]);

  const devices = [
    ...mediaDevices.videoinputs.map((value) => ({
      kind: value.kind,
      id: value.deviceId,
      label: value.label,
      title: "Video Input",
      isSelected: value.deviceId === mediaDevices.currentVideoInput,
    })),
    ...mediaDevices.audioinputs.map((value) => ({
      kind: value.kind,
      id: value.deviceId,
      label: value.label,
      title: "Audio Input",
      isSelected: value.deviceId === mediaDevices.currentAudioInput,
    })),
  ];

  const onChange = (element) => {
    setCurrentMediaDevices({
      [element.kind]: element.id,
    });
  };

  return (
    // eslint-disable-next-line jsx-a11y/no-static-element-interactions
    <div
      ref={ref}
      className={`vclerk-settings${open ? " vclerk-settings-show" : ""}${
        position === "below" ? " vclerk-settings-below" : ""
      }`}
      onClick={(e) => e.preventDefault()}
    >
      <Dropdown elements={devices} onChange={onChange} />
    </div>
  );
};

DropdownDevices.defaultProps = {
  open: false,
  onClickOut: () => {},
};

DropdownDevices.propTypes = {
  open: PropTypes.bool,
  onClickOut: PropTypes.func,
};

export default DropdownDevices;
