/* eslint-disable react/no-array-index-key */
import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import I18n from "i18n";
import Star from "assets/icons/Star";
import "./index.scss";

const Rating = ({ maxRate, setRating }) => {
  const defaultState = {
    ratings: [...Array(maxRate)].fill({
      active: false,
    }),
    rated: undefined,
  };

  const [state, setState] = useState(defaultState);

  useEffect(() => {
    if (state.rated) {
      setRating(state.rated);
    }
  }, [state.rated]);

  const handleRatingHover = (elIndex) => {
    setState((currState) => ({
      ...currState,
      ratings: currState.ratings.map((e, i) => ({
        active: i <= elIndex,
      })),
    }));
  };

  const handleMouseEnter = (elIndex) => handleRatingHover(elIndex);
  const handleMouseLeave = (elIndex) => handleRatingHover(elIndex);

  const handleMouseLeaveContainer = () => {
    setState((currState) => ({
      ...currState,
      ratings: currState.ratings.map((e, i) => ({
        active: i < state.rated,
      })),
    }));
  };

  const onClickHandle = (rated) => {
    setState((currState) => ({
      ...currState,
      rated,
    }));
  };

  return (
    <div
      className="vclerk-rating-wrapper"
      onMouseLeave={handleMouseLeaveContainer}
    >
      <div className="vclerk-rating-title">{I18n.t("outro.ratingTitle")}</div>
      <div className="vclerk-rating-container">
        {state.ratings.map((e, i) => (
          <div
            role="button"
            tabIndex={0}
            key={`rating-${i}`}
            onMouseEnter={() => handleMouseEnter(i)}
            onMouseLeave={() => handleMouseLeave(i)}
            onClick={() => onClickHandle(i + 1)}
          >
            <Star color="#080a1e" active={e.active} width={26} height={26} />
          </div>
        ))}
      </div>
    </div>
  );
};

Rating.defaultProps = {
  maxRate: 5,
};

Rating.propTypes = {
  maxRate: PropTypes.number,
  setRating: PropTypes.func.isRequired,
};

export default Rating;
