/* eslint-disable react/jsx-wrap-multilines */
import React, { useState } from "react";
import PropTypes from "prop-types";
import I18n from "i18n";
import Checkbox from "src/components/Checkbox";
import Button from "src/components/Button";
import Close from "assets/icons/close";
import VClerkLogo from "assets/icons/VClerkLogo";
import "./index.scss";

const Intro = ({ onConfirm, onClose }) => {
  const [hasConfirmedGDPR, setHasConfirmedGDPR] = useState(undefined);

  return (
    <div className="vclerk-widget-intro">
      <div className="vclerk-widget-intro-logo">
        <VClerkLogo color="#f4f3f2" width={56} height={16} />
      </div>
      <div
        role="button"
        className="vclerk-widget-intro-close"
        onClick={onClose}
        tabIndex={0}
      >
        <Close color="#080a1e" width={20} height={20} />
      </div>
      <div className="vclerk-widget-intro-description">
        <span>{I18n.t("intro.description")}</span>
        <span>{I18n.t("intro.description_subtext")}</span>
      </div>
      <div className="vclerk-widget-intro-confirm-wrapper">
        <Checkbox
          onChange={(e) => {
            setHasConfirmedGDPR(e);
          }}
          hasError={
            typeof hasConfirmedGDPR !== "undefined" && !hasConfirmedGDPR
          }
          id="gdpr-confirm"
          label={
            <>
              {I18n.t("intro.terms")}
              &nbsp;
              <a
                className="vclerk-widget-terms_link"
                target="_blank"
                rel="noreferrer"
                href={I18n.t("intro.terms_link")}
              >
                {I18n.t("intro.terms_subject")}
              </a>
              .
            </>
          }
        />
      </div>
      <Button
        style={{
          fontSize: 23,
          fontWeight: 400,
          letterSpacing: 1,
        }}
        onClick={() => {
          if (!hasConfirmedGDPR) return hasConfirmedGDPR;
          return onConfirm();
        }}
      >
        {I18n.t("intro.start_vclerk")}
      </Button>
    </div>
  );
};

Intro.propTypes = {
  onConfirm: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
};

export default Intro;
