/* eslint-disable react/jsx-props-no-spreading */
import React from "react";
import ReactDOM from "react-dom";
import * as Sentry from "@sentry/browser";
import ProductWidget from "./src";
import { setTheme } from "./src/utils/theme";

let IS_MOUNTED = false;

// eslint-disable-next-line import/prefer-default-export
export const init = (selector, config, theme, onClose) => {
  setTheme(theme);
  IS_MOUNTED = true;
  const closeWidget = () => {
    if (IS_MOUNTED) {
      IS_MOUNTED = false;
      ReactDOM.unmountComponentAtNode(selector);
      onClose();
    }
  };

  const environment =
    typeof process.env.VCLERK_ENV !== "undefined"
      ? process.env.VCLERK_ENV
      : "PROD";
  Sentry.init({
    dsn: process.env.VCLERK_SENTRY_DSN,
    release: process.env.VCLERK_RELEASE,
    environment,
    debug: environment === "DEV",
    // This works because 'videoClerk'/'vclerk' is contained in the URL of our CDN
    whitelistUrls:
      environment === "DEV" ? undefined : [/.*videoClerk.*/, /.*vclerk.*/],
  });

  ReactDOM.render(
    <ProductWidget {...config} onClose={closeWidget} />,
    selector
  );
};
