import React from "react";
import PropTypes from "prop-types";
import * as Sentry from "@sentry/browser";
import I18n from "i18n";
import Button from "src/components/Button";
import ErrorIcon from "assets/icons/error";
import CloseIcon from "assets/icons/close";
import "./index.scss";

class VClerkErrorBoundary extends React.Component {
  constructor() {
    super();
    this.state = { hasError: false, message: null };
  }

  static getDerivedStateFromError() {
    return { hasError: true };
  }

  componentDidCatch(error, errorInfo) {
    this.setState((state) => ({
      ...state,
      message: error?.i18nMessage ?? null,
    }));
    Sentry.withScope((scope) => {
      scope.setExtras(errorInfo);
      Sentry.captureException(error);
    });
  }

  render() {
    const { hasError, message } = this.state;
    const { children, onClose } = this.props;

    if (hasError) {
      return (
        <div className="vclerk-error-wrapper">
          <div
            role="button"
            className="vclerk-error-wrapper-close"
            onClick={onClose}
            tabIndex={0}
          >
            <CloseIcon color="#000c44" />
          </div>
          <ErrorIcon color="#000c44" width={100} height={100} />
          <div className="vclerk-error-heading">
            {message || I18n.t("error.something_went_wrong")}
          </div>
          <Button onClick={onClose}>{I18n.t("error.close")}</Button>
        </div>
      );
    }

    return children;
  }
}

VClerkErrorBoundary.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.node),
  ]).isRequired,
  onClose: PropTypes.func.isRequired,
};

export default VClerkErrorBoundary;
