import states from "utils/states";

export default ({ setRoom, widgetState }) => [
  {
    eventName: "connect",
    action: () =>
      setRoom((state) => ({ ...state, connected: true, clerk_present: true })),
  },
  // This is used to update the position and estimated time
  {
    eventName: "position_updated",
    action: (data) => setRoom((state) => ({ ...state, ...data })),
  },
  // This is received when the dashboard emits `next_guest`
  // That means that the clerk is picking this customer to serve
  {
    eventName: "clerk_assigned",
    action: (data) => setRoom((state) => ({ ...state, ...data })),
  },
  {
    eventName: "clerk_abandon",
    action: (data) => {
      if (data.user_type === "clerk") {
        setRoom((state) => {
          if (
            widgetState === states.mediaAccessed &&
            data.clerk_id === state.clerk_id
          ) {
            return { ...state, queue_id: null };
          }
          return { ...state }; // Not ideal, it refreshes the dom for nothing?
        });
      }
    },
  },
  {
    eventName: "queue_has_no_clerks",
    action: (data) =>
      setRoom((state) => {
        if (state.queue_id === data.queue_id) {
          return { ...state, clerk_present: false };
        }
        return { ...state };
      }),
  },
  {
    eventName: "cant_join_queue",
    action: () =>
      setRoom(
        (state) =>
          // TODO: Change when we support multiple queues
          ({ ...state, clerk_present: false })
        // if (state.queue_id === data.queue_id) {
        //   return { ...state, clerk_present: false };
        // }
        // return { ...state };
      ),
  },
];
