/* eslint-disable camelcase */
import React, { useState, useEffect, useRef, useReducer } from "react";
import PropTypes from "prop-types";
import I18n from "i18n";
import { SkylinkConstants, SkylinkEventManager } from "skylinkjs";
import { setSkylinkKey } from "utils/skylink";
import useEventListener from "hooks/useEventListener";
import useWidgetState from "hooks/useWidgetState";
import mediaDevicesReducer from "reducers/mediaDevicesReducer";
import { initSockets, emit, disconnectSocket } from "sockets";
import states from "utils/states";
import notification from "utils/notification";

export const WidgetContext = React.createContext();

export default function WidgetProvider({
  children,
  queue,
  apiKey,
  skylink_key,
}) {
  const { widgetState, setWidgetState } = useWidgetState();
  const [streamMuted, setStreamMuted] = useState({
    audio: false,
    video: false,
  });
  const [mediaDevices, dispatchMediaDevices] = useReducer(mediaDevicesReducer, {
    videoinputs: [],
    audioinputs: [],
    currentVideoInput: null,
    currentAudioInput: null,
  });

  function setCurrentMediaDevices({ videoinput, audioinput }) {
    return dispatchMediaDevices({
      type: "set-currents",
      data: {
        videoinput,
        audioinput,
      },
    });
  }

  const [room, setRoom] = useState({
    connected: false,
  });
  const isPeerPresent = useRef();

  useEffect(() => {
    try {
      setSkylinkKey(skylink_key);
    } catch (err) {
      if (err.message === "WebRTC not supported. Please upgrade your browser") {
        err.i18nMessage = I18n.t("error.webrtc_not_supported");
        throw err;
      }
    }
  }, [skylink_key]);

  useEffect(() => {
    if (widgetState === states.mediaAccessed) {
      initSockets(
        { setRoom, widgetState, apiKey },
        { query: { "x-videoClerk-api-key": apiKey } }
      );
    }
    if (widgetState === states.connecting) {
      notification();
    }
    if (widgetState === states.connected) {
      isPeerPresent.current = true;
    }
  }, [widgetState]);

  const onPeerLeft = () => {
    if (isPeerPresent.current && widgetState !== states.outro) {
      isPeerPresent.current = false;
      setWidgetState(states.outro);
    }
  };

  const onSessionDisconnect = () => {
    const error = new Error("Session was disconnected");
    error.i18nMessage = I18n.t("error.session_disconnect");
    throw error;
  };

  useEventListener(
    SkylinkConstants.EVENTS.PEER_LEFT,
    onPeerLeft,
    SkylinkEventManager
  );

  useEventListener(
    SkylinkConstants.EVENTS.SESSION_DISCONNECT,
    onSessionDisconnect,
    SkylinkEventManager
  );

  useEffect(() => () => disconnectSocket(), []);

  useEffect(() => {
    if (room.connected) {
      emit("guestJoinsQueues", {
        queue_ids: [queue],
      });
    }
  }, [room.connected]);

  useEffect(() => {
    if (room.clerk_present === false) {
      const err = new Error("No clerk present");
      err.i18nMessage = I18n.t("error.no_clerk_present");
      disconnectSocket();
      throw err;
    }
  }, [room.clerk_present]);

  useEffect(() => {
    if (room.clerk_id) {
      setWidgetState(states.connecting);
    }
  }, [room.clerk_id]);

  useEffect(() => {
    if (!room.queue_id && widgetState === states.connecting) {
      emit("guestJoinsQueues", {
        queue_ids: [queue],
      });
    }
  }, [room.queue_id]);

  function enumerateDevices() {
    // polyfilled by Temasys/AdapterJS
    // eslint-disable-next-line compat/compat
    navigator.mediaDevices.enumerateDevices().then((devices) => {
      dispatchMediaDevices({
        type: "set-devices",
        data: devices,
      });
    });
  }

  useEffect(() => {
    if (widgetState === states.mediaAccessed) {
      enumerateDevices();
    }
    // eslint-disable-next-line compat/compat
    navigator.mediaDevices.addEventListener("devicechange", enumerateDevices);
    return () => {
      // eslint-disable-next-line compat/compat
      navigator.mediaDevices.removeEventListener(
        "devicechange",
        enumerateDevices
      );
    };
  }, [widgetState]);

  return (
    <WidgetContext.Provider
      value={{
        widgetState,
        setWidgetState,
        streamMuted,
        setStreamMuted,
        room,
        apiKey,
        mediaDevices,
        setCurrentMediaDevices,
      }}
    >
      {children}
    </WidgetContext.Provider>
  );
}

WidgetProvider.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.node),
  ]).isRequired,
  queue: PropTypes.string.isRequired,
  apiKey: PropTypes.string.isRequired,
  skylink_key: PropTypes.string.isRequired,
};
