import React, { useEffect, useContext } from "react";
import PropTypes from "prop-types";
import I18n from "i18n";
import skylink from "utils/skylink";
import { WidgetContext } from "context/widgetContext";
import states from "utils/states";
import PeerVideo from "src/components/PeerVideo";
import ProductList from "src/components/ProductList";
import Product from "src/components/Product";
import SelfVideo from "src/components/SelfVideo";
import VClerkLogo from "assets/icons/VClerkLogo";
import Close from "assets/icons/close";
import Queue from "src/screens/Conversation/Queue";
import { ConversationContext } from "src/context/conversationContext";
import Outro from "./Outro";

import "./index.scss";

const Conversation = ({ onClose }) => {
  const { room, widgetState } = useContext(WidgetContext);
  const { loading, handleClosing } = useContext(ConversationContext);

  useEffect(() => {
    if (room?.guest_id) {
      skylink()
        .joinRoom({
          roomName: room.guest_id,
        })
        .catch((err) => {
          throw err;
        });
    }
  }, [room?.guest_id]);

  if (loading) return <Queue onClose={onClose} />;

  return (
    <div
      className="vclerk-cv-wrapper"
      style={widgetState === states.outro ? { overflowY: "hidden" } : {}}
    >
      <div className="vclerk-cv-wrapper-logo">
        <VClerkLogo color="#f4f3f2" width={56} height={16} />
      </div>
      {widgetState === states.outro && <Outro onClose={onClose} />}
      <div className="vclerk-cv-row">
        <div className="vclerk-cv-videos-wrapper" id="vclerk-videos-container">
          <PeerVideo />
          <SelfVideo
            isFromCustomer
            onClose={() => handleClosing()}
            wrapperStyle={{
              flex: "1 1 0",
              alignSelf: "stretch",
              paddingRight: 12,
            }}
          />
        </div>
        <div className="vclerk-cv-pl-title">{I18n.t("in_call.showroom")}</div>
        <div className="vclerk-cv-pl-wrapper">
          <ProductList />
        </div>
      </div>
      <div className="vclerk-cv-p-wrapper">
        <Product />
      </div>
      <div
        tabIndex={0}
        role="button"
        className="vclerk-cv-close-sm"
        onClick={() => handleClosing()}
      >
        <Close />
      </div>
    </div>
  );
};

Conversation.defaultProps = {
  onClose: () => {},
};

Conversation.propTypes = {
  onClose: PropTypes.func,
};

export default Conversation;
