/* eslint-disable camelcase */
import React, { useState, useEffect, useContext } from "react";
import PropTypes from "prop-types";
import { SkylinkConstants, SkylinkEventManager } from "skylinkjs";
import skylink, { p2pMessageActions, sendData } from "utils/skylink";
import useEventListener from "hooks/useEventListener";
import { WidgetContext } from "context/widgetContext";
import checkSchema from "utils/productSchema";
import states from "utils/states";
import unknownPromise from "utils/unknownPromise";

export const ConversationContext = React.createContext();

let initialProductsAndCart;
let streamPromise;
let selfStreams;

function stopTracks(tracks) {
  tracks.forEach((track) => {
    track.stop();
  });
}

function toggleMuteTracks(tracks) {
  tracks.forEach((track) => {
    // eslint-disable-next-line no-param-reassign
    track.enabled = !track.enabled;
  });
}

export default function SkylinkProvider({
  children,
  onProductAdd,
  onGoToCart,
  initialProducts,
  initialCart,
}) {
  const [loading, setLoading] = useState(true);
  const [peerStreams, setPeerStreams] = useState([]);
  const [products, setProducts] = useState([]);
  const [imgDetailPosition, setImgDetailPosition] = useState();
  const [activeProduct, setActiveProduct] = useState({
    id: null,
    mediaIndex: 0,
  });

  const {
    room,
    streamMuted,
    mediaDevices,
    setCurrentMediaDevices,
    widgetState,
    setWidgetState,
  } = useContext(WidgetContext);

  const [productsAddedToCartIds, setProductsAddedToCartIds] = useState([]);

  useEffect(() => {
    if (
      selfStreams &&
      (mediaDevices.currentAudioInput || mediaDevices.currentVideoInput)
    ) {
      const audioChanged =
        mediaDevices.currentAudioInput !==
        selfStreams[0].getAudioTracks()[0].getSettings().deviceId;
      const videoChanged =
        mediaDevices.currentVideoInput !==
        selfStreams[1].getVideoTracks()[0].getSettings().deviceId;

      if (audioChanged || videoChanged) {
        const constraints = {
          audio: true,
          video: true,
        };

        if (mediaDevices.currentVideoInput) {
          constraints.video = {
            deviceId: mediaDevices.currentVideoInput,
          };
        }

        if (mediaDevices.currentAudioInput) {
          constraints.audio = {
            deviceId: mediaDevices.currentAudioInput,
          };
        }

        const getUserMedia = skylink().getUserMedia(constraints);
        streamPromise.finally(() => {
          stopTracks([
            ...selfStreams[0].getTracks(),
            ...selfStreams[1].getTracks(),
          ]);
          streamPromise = getUserMedia.then((streams) => {
            selfStreams = streams;
            setCurrentMediaDevices({
              audioinput: streams[0].getAudioTracks()[0].getSettings().deviceId,
              videoinput: streams[1].getVideoTracks()[0].getSettings().deviceId,
            });
            toggleMuteTracks([
              ...(streamMuted.audio ? streams[0].getTracks() : []),
              ...(streamMuted.video ? streams[1].getTracks() : []),
            ]);
            if (room.guest_id) {
              skylink().sendStream(room.guest_id, streams);
            }
          });
        });
      }
    }
  }, [mediaDevices.currentVideoInput, mediaDevices.currentAudioInput]);

  useEffect(() => {
    if (widgetState === states.outro) {
      skylink().leaveAllRooms();
      if (selfStreams) {
        stopTracks([
          ...(selfStreams[0] ? selfStreams[0].getTracks() : []),
          ...(selfStreams[1] ? selfStreams[1].getTracks() : []),
        ]);
      }
    }
  }, [widgetState]);

  useEffect(() => {
    if (selfStreams) {
      toggleMuteTracks([
        ...(selfStreams[0].getTracks()[0].enabled === streamMuted.audio
          ? selfStreams[0].getTracks()
          : []),
        ...(selfStreams[1].getTracks()[0].enabled === streamMuted.video
          ? selfStreams[1].getTracks()
          : []),
      ]);
      if (widgetState === states.connected) {
        const { type, methods } = p2pMessageActions.userAction;
        sendData({
          room: room.guest_id,
          type,
          method: methods.updateMuteStatus,
          data: streamMuted,
        });
      }
    }
  }, [streamMuted, widgetState]);

  const handleProductAddedToCart = (product) => {
    if (room?.guest_id && product) {
      return unknownPromise(() => onProductAdd(product)).then(() => {
        setProductsAddedToCartIds((prevCartExtIds) => [
          ...prevCartExtIds,
          product.external_id,
        ]);
        const { type, methods } = p2pMessageActions.userAction;
        sendData({
          room: room.guest_id,
          type,
          method: methods.addToCart,
          data: {
            product_id: product.id,
          },
        });
      });
    }
    return Promise.resolve();
  };

  const handleClosing = () => {
    setWidgetState(states.outro);
  };

  const onIncomingMessage = (evt) => {
    const { content } = evt.detail.message;
    const json = JSON.parse(content);
    if (!evt.detail.isSelf) {
      if (json.type === p2pMessageActions.init.type) {
        const { methods } = p2pMessageActions.init;
        if (json.method === methods.res) {
          checkSchema(json.products);
          setProducts(json.products);
          setActiveProduct({
            id: json.products[0].id,
            mediaIndex: 0,
          });
          setLoading(false);
        }
      }
    }
    if (json.type === p2pMessageActions.userAction.type) {
      const { methods } = p2pMessageActions.userAction;
      if (json.method === methods.productView) {
        setActiveProduct({
          id: json.product_id,
          mediaIndex: json.media_index,
        });
      }
      if (json.method === methods.productRemove) {
        setProducts((prevProducts) =>
          prevProducts.filter((p) => p.id !== json.product_id)
        );
      }
      if (json.method === methods.productAdd) {
        setProducts((prevProducts) => {
          const index = prevProducts.findIndex(
            (p) => p.item_group_id === json.product.item_group_id
          );
          let prevProdsCopy = [...prevProducts];
          if (index >= 0) {
            prevProdsCopy[index] = json.product;
          } else {
            prevProdsCopy = [...prevProdsCopy, json.product];
          }

          return prevProdsCopy;
        });

        setActiveProduct({
          mediaIndex: 0,
          id: json.product.id,
        });
      }

      if (json.method === methods.setProductImageDetail) {
        setImgDetailPosition(json);
      }
      if (json.method === methods.resetProductImageDetail) {
        setImgDetailPosition(undefined);
      }

      if (json.method === methods.setProductImageDetail) {
        setImgDetailPosition(json);
      }
      if (json.method === methods.resetProductImageDetail) {
        setImgDetailPosition(undefined);
      }
    }
  };

  const getInitialProducts = (evt) => {
    if (!!evt.detail.state && evt.detail.state === "open") {
      const { type, methods } = p2pMessageActions.init;
      sendData({
        room: room.guest_id,
        type,
        method: methods.req,
        data: {
          initial_product_ids: initialProductsAndCart?.initial || [],
          initial_cart_ids: initialProductsAndCart?.cart || [],
        },
      });
    }
  };

  const onIncomingStream = (evt) => {
    if (evt.detail.isSelf) return;
    setPeerStreams((prevStreams) => ({
      audio: evt.detail.isAudio ? evt.detail : prevStreams.audio,
      video: evt.detail.isVideo ? evt.detail : prevStreams.video,
    }));
  };

  const onPeerJoined = (evt) => {
    if (evt.detail.isSelf) return;
    skylink().sendStream(room.guest_id, selfStreams);
  };

  useEventListener(
    SkylinkConstants.EVENTS.PEER_JOINED,
    onPeerJoined,
    SkylinkEventManager
  );
  useEventListener(
    SkylinkConstants.EVENTS.ON_INCOMING_STREAM,
    onIncomingStream,
    SkylinkEventManager
  );
  useEventListener(
    SkylinkConstants.EVENTS.ON_INCOMING_MESSAGE,
    onIncomingMessage,
    SkylinkEventManager
  );
  useEventListener(
    SkylinkConstants.EVENTS.DATA_CHANNEL_STATE,
    getInitialProducts,
    SkylinkEventManager
  );

  useEffect(() => {
    Promise.allSettled([
      unknownPromise(initialProducts),
      unknownPromise(initialCart),
    ]).then(([initP, initC]) => {
      initialProductsAndCart = {
        initial: initP.status === "fulfilled" ? initP.value : [],
        cart: initC.status === "fulfilled" ? initC.value : [],
      };
      setProductsAddedToCartIds((prevCartExtIds) => [
        ...prevCartExtIds,
        ...initialProductsAndCart.cart,
      ]);
    });

    streamPromise = skylink().getUserMedia({
      audio: true,
      video: {
        facingMode: "user",
      },
    });

    const accessMediaDevices = () => {
      streamPromise
        .then((streams) => {
          selfStreams = streams;
          setWidgetState(states.mediaAccessed);
          setCurrentMediaDevices({
            audioinput: streams[0].getAudioTracks()[0].getSettings().deviceId,
            videoinput: streams[1].getVideoTracks()[0].getSettings().deviceId,
          });
        })
        .catch(() => {
          // TODO: Handle errors better (https://blog.addpipe.com/common-getusermedia-errors/)
          // TODO: Make stats about % of people with camera/microphone issues?
          setWidgetState(states.mediaRejected);
        });
    };
    accessMediaDevices();

    return () => {
      setTimeout(() => {
        skylink().leaveAllRooms();
        if (selfStreams) {
          stopTracks([
            ...(selfStreams[0] ? selfStreams[0].getTracks() : []),
            ...(selfStreams[1] ? selfStreams[1].getTracks() : []),
          ]);
        }
      }, 750);
    };
  }, []);

  return (
    <ConversationContext.Provider
      value={{
        loading,
        peerStreams,
        products,
        activeProduct,
        selfStreams,
        handleProductAddedToCart: onProductAdd
          ? handleProductAddedToCart
          : null,
        onGoToCart,
        productsAddedToCartIds,
        handleClosing,
        imgDetailPosition,
      }}
    >
      {children}
    </ConversationContext.Provider>
  );
}

SkylinkProvider.defaultProps = {
  onProductAdd: null,
  onGoToCart: null,
  initialProducts: [],
  initialCart: [],
};

SkylinkProvider.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.node),
  ]).isRequired,
  onProductAdd: PropTypes.func,
  onGoToCart: PropTypes.func,
  initialProducts: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.string),
    PropTypes.func,
  ]),
  initialCart: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.string),
    PropTypes.func,
  ]),
};
