import React, { useContext, useEffect, useState } from "react";
import PropTypes from "prop-types";
import { SkylinkEventManager, SkylinkConstants } from "skylinkjs";
import skylink, { p2pMessageActions } from "utils/skylink";
import { WidgetContext } from "context/widgetContext";
import useEventListener from "hooks/useEventListener";
import DropdownDevices from "src/components/DropdownDevices";
import MicrophoneOn from "assets/icons/MicrophoneOn";
import MicrophoneOff from "assets/icons/MicrophoneOff";
import Settings from "assets/icons/Settings";
import CameraOn from "assets/icons/CameraOn";
import CameraOff from "assets/icons/CameraOff";
import states from "utils/states";
import "./index.scss";

const VideoControllers = ({
  clickable,
  iconSizes,
  iconTextSize,
  room,
  self,
}) => {
  const {
    streamMuted: selfStreamMuted,
    setStreamMuted: setSelfStreamMuted,
    widgetState,
  } = useContext(WidgetContext);

  const [peerStreamMuted, setPeerStreamMuted] = useState({
    audio: false,
    video: false,
  });

  const [openSettings, setOpenSettings] = useState(false);

  useEffect(() => {
    // Its here because there seems to be an issue with
    // state initiallization and the way skylink gets the peers in the room
    // basically on state initializations the room doesn't exist yet...
    const getUserMedia = () => {
      if (widgetState === states.connected) {
        const skylinkInstance = skylink();
        const peers = skylinkInstance.getPeersInRoom(room.guest_id);
        const pValues = Object.values(peers);
        const defaultValue = {
          audio: false,
          video: false,
        };
        for (let i = 0; i < pValues.length; i += 1) {
          if (pValues[i].isSelf === self) {
            const mediaS = Object.values(pValues[i].mediaStatus);
            for (let x = 0; x < mediaS.length; x += 1) {
              Object.entries(mediaS[x]).forEach(([key, value]) => {
                if (value >= 0) {
                  if (key.startsWith("audio")) {
                    defaultValue.audio = !value;
                  }
                  if (key.startsWith("video")) {
                    defaultValue.video = !value;
                  }
                }
              });
            }
          }
        }

        setPeerStreamMuted(defaultValue);
      }
    };

    getUserMedia();
  }, []);

  const handleMuteStream = (evt) => {
    const { streamId, isAudio, peerInfo, isSelf } = evt.detail;
    if (self || isSelf) return;
    const type = isAudio ? "audio" : "video";
    setPeerStreamMuted({
      ...peerStreamMuted,
      [type]: !peerInfo.mediaStatus[streamId][`${type}Muted`],
    });
  };

  // Future proofing.
  const handleMuteStreamMessage = (evt) => {
    if (self || evt.detail.isSelf) return;
    const { content } = evt.detail.message;
    const json = JSON.parse(content);
    if (json.type === p2pMessageActions.userAction.type) {
      const { methods } = p2pMessageActions.userAction;
      if (json.method === methods.updateMuteStatus) {
        setPeerStreamMuted(json);
      }
    }
  };

  useEventListener(
    SkylinkConstants.EVENTS.ON_INCOMING_MESSAGE,
    handleMuteStreamMessage,
    SkylinkEventManager
  );

  useEventListener(
    SkylinkConstants.EVENTS.STREAM_MUTED,
    handleMuteStream,
    SkylinkEventManager
  );

  const mute = ({ audio, video }) => {
    const audioMuted = audio === undefined ? selfStreamMuted?.audio : audio;
    const videoMuted = video === undefined ? selfStreamMuted?.video : video;
    setSelfStreamMuted({
      audio: audioMuted,
      video: videoMuted,
    });
  };

  if (!room || !room.guest_id) return null;

  return (
    <div
      className={`vclerk-sv-mute-container ${!self ? "vclerk-pv-mute-sc" : ""}`}
    >
      {(!self && peerStreamMuted?.audio) || (self && selfStreamMuted?.audio) ? (
        <div
          className="vclerk-icon-container vclerk-icon-off"
          tabIndex={0}
          role="button"
          aria-label="Unnmute"
          style={{ ...(iconTextSize && { fontSize: iconTextSize }) }}
          onClick={() => clickable && mute({ audio: false })}
        >
          <MicrophoneOff width={iconSizes.width} height={iconSizes.height} />
        </div>
      ) : (
        self && (
          <div
            className="vclerk-icon-container vclerk-icon-on"
            tabIndex={0}
            role="button"
            aria-label="Mute"
            style={{ ...(iconTextSize && { fontSize: iconTextSize }) }}
            onClick={() => clickable && mute({ audio: true })}
          >
            <MicrophoneOn
              color="#080a1e"
              width={iconSizes.width}
              height={iconSizes.height}
            />
          </div>
        )
      )}
      {(!self && peerStreamMuted?.video) || (self && selfStreamMuted?.video) ? (
        <div
          className="vclerk-icon-container vclerk-icon-off"
          tabIndex={0}
          role="button"
          aria-label="Show camera"
          onClick={() => clickable && mute({ video: false })}
          style={{
            ...(iconTextSize && { fontSize: iconTextSize }),
            cursor: clickable ? "pointer" : "default",
          }}
        >
          <CameraOff width={iconSizes.width} height={iconSizes.height} />
        </div>
      ) : (
        self && (
          <div
            className="vclerk-icon-container vclerk-icon-on"
            tabIndex={0}
            role="button"
            aria-label="Hide camera"
            style={{ ...(iconTextSize && { fontSize: iconTextSize }) }}
            onClick={() => clickable && mute({ video: true })}
          >
            <CameraOn
              color="#080a1e"
              width={iconSizes.width}
              height={iconSizes.height}
            />
          </div>
        )
      )}
      {self && clickable && (
        <div className="vclerk-setting-button">
          <div
            className="vclerk-icon-container vclerk-icon-on"
            tabIndex={0}
            role="button"
            aria-label="Settings"
            style={{ ...(iconTextSize && { fontSize: iconTextSize }) }}
            onClick={() => setOpenSettings(!openSettings)}
          >
            <Settings
              color="#080a1e"
              width={iconSizes.width - 2}
              height={iconSizes.height - 2}
            />
          </div>
          <DropdownDevices
            open={openSettings}
            onClickOut={() => setOpenSettings(false)}
          />
        </div>
      )}
    </div>
  );
};

VideoControllers.defaultProps = {
  clickable: true,
  iconSizes: {
    width: 22,
    height: 22,
  },
  iconTextSize: 12,
  self: true,
  room: undefined,
};

VideoControllers.propTypes = {
  clickable: PropTypes.bool,
  room: PropTypes.shape({
    guest_id: PropTypes.string,
  }),
  iconSizes: PropTypes.shape({
    width: PropTypes.number,
    height: PropTypes.number,
  }),
  iconTextSize: PropTypes.number,
  self: PropTypes.bool,
};

const shouldUpdate = (prevProps, nextProp) =>
  nextProp.room?.guest_id === prevProps.room?.guest_id;

export default React.memo(VideoControllers, shouldUpdate);
