export default function mediaDevicesReducer(state, action) {
  switch (action.type) {
    case "set-currents":
      return {
        ...state,
        currentVideoInput: action?.data?.videoinput
          ? action.data.videoinput
          : state.currentVideoInput,
        currentAudioInput: action?.data?.audioinput
          ? action.data.audioinput
          : state.currentAudioInput,
      };
    case "set-devices": {
      const newState = {
        ...state,
        videoinputs: action.data.filter((input) => input.kind === "videoinput"),
        audioinputs: action.data.filter((input) => input.kind === "audioinput"),
      };
      if (
        newState.currentVideoInput &&
        newState.videoinputs &&
        newState.videoinputs.findIndex(
          (el) => el.deviceId === newState.currentVideoInput
        ) === -1
      ) {
        newState.currentVideoInput = newState.videoinputs[0].deviceId;
      }
      if (
        newState.currentAudioInput &&
        newState.audioinputs &&
        newState.audioinputs.findIndex(
          (el) => el.deviceId === newState.currentAudioInput
        ) === -1
      ) {
        newState.currentAudioInput = newState.audioinputs[0].deviceId;
      }
      return newState;
    }
    default:
      throw new Error("mediaDevicesReducer: did not get correct action type");
  }
}
