import React from "react";
import PropTypes from "prop-types";
import "./index.scss";

const Button = ({ onClick, tabIndex, children, style }) => (
  <div
    style={style}
    className="vclerk-wizard-button"
    onClick={onClick}
    role="button"
    tabIndex={tabIndex}
  >
    {children}
  </div>
);

Button.defaultProps = {
  tabIndex: 0,
  style: {},
};

Button.propTypes = {
  tabIndex: PropTypes.number,
  children: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.node),
  ]).isRequired,
  onClick: PropTypes.func.isRequired,
  style: PropTypes.shape({}),
};

export default Button;
