import React, { useContext } from "react";
import { sendData, p2pMessageActions } from "utils/skylink";
import { WidgetContext } from "context/widgetContext";
import { ConversationContext } from "context/conversationContext";
import "./index.scss";

const ProductList = () => {
  const { room } = useContext(WidgetContext);
  const { products, activeProduct } = useContext(ConversationContext);

  if (!activeProduct && !activeProduct.id) return null;

  const clickedProduct = (product) => {
    if (room?.guest_id) {
      const { type, methods } = p2pMessageActions.userAction;
      sendData({
        room: room.guest_id,
        type,
        method: methods.productView,
        data: {
          product_id: product.id,
          media_index: 0,
        },
      });
    }
  };

  return (
    <div className="vclerk-pl-wrapper">
      {products &&
        products.map((p) => (
          <div
            className={`vclerk-pl-item ${
              p.id === activeProduct.id ? "active-product" : ""
            }`}
            key={`${p.id}-${activeProduct.id}`}
            role="button"
            tabIndex={0}
            onClick={() => clickedProduct(p)}
          >
            <div className="img-container">
              <img
                className="wizard-product-image"
                src={p.image_link_thumbnail || p.image_link_cdn || p.image_link}
                alt="product"
              />
            </div>
            <div className="p-item-info">
              <div className="p-description">{p.title}</div>
              <div className="p-price">
                {`${p.currency} ${p.sale_price ?? p.price}`}
              </div>
            </div>
          </div>
        ))}
    </div>
  );
};

export default React.memo(ProductList);
