/* eslint-disable jsx-a11y/control-has-associated-label */
import React, { useState, useEffect, useContext, useRef } from "react";
import PropTypes from "prop-types";
import { sendData, p2pMessageActions } from "utils/skylink";
import { WidgetContext } from "context/widgetContext";
import { ConversationContext } from "src/context/conversationContext";
import usePrevious from "hooks/usePrevious";
import Arrow from "assets/icons/arrow";
import CloseIcon from "assets/icons/closeIcon";
import "./index.scss";

const loadedImages = [];

const imageSizes = "(max-width: 768px) 100vh, 33vh"; // 100vh on mobile, roughly 1/3vh on desktop

const sizes = [1400, 1000, 800];
function getSrcSetFromItem(item) {
  let srcSet = "";

  sizes.forEach((size) => {
    if (item.width >= size) {
      if (srcSet.length) {
        srcSet += `, `;
      }
      srcSet += `${item[`url_w${size}`] || item.url} ${size}w`;
    }
  });
  if (!srcSet.length) {
    srcSet += `${item.url_cdn || item.url} 800w`;
  }

  return srcSet;
}

const ImageRotator = ({ items, productId, mediaIndex }) => {
  const { room } = useContext(WidgetContext);
  const { imgDetailPosition } = useContext(ConversationContext);
  const [activeImage, setActiveImage] = useState(mediaIndex);
  const prevItemsLength = usePrevious(items?.length || 0);
  const magnifierRef = useRef({});
  const imgRef = useRef();
  const hasNext = activeImage + 1 < items?.length;
  const hasPrev = activeImage >= 1;

  useEffect(() => {
    if (prevItemsLength !== items?.length) {
      setActiveImage(0);
    }
  });

  useEffect(() => {
    if (items) {
      for (let step = -1; step <= 1; step += 1) {
        const currentIndex = mediaIndex + step;
        const item = items[currentIndex];
        if (currentIndex > 0 && item && !loadedImages.includes(item?.url)) {
          const img = new Image();
          img.sizes = imageSizes;
          img.srcset = getSrcSetFromItem(item);
          img.src = item.url_cdn || item.url;
          loadedImages.push(item.url);
        }
      }
    }
  }, [items, mediaIndex]);

  const onChangeImageIndex = (newIndex) => {
    setActiveImage(newIndex);
    if (room) {
      const { type, methods } = p2pMessageActions.userAction;
      sendData({
        room: room.guest_id,
        type,
        method: methods.productView,
        data: {
          product_id: productId,
          media_index: newIndex,
        },
      });
    }
  };

  let touchStartX = null;
  let touchStartY = null;
  const handleTouchStart = (evt) => {
    const touch = evt.touches[0];
    touchStartX = touch.clientX;
    touchStartY = touch.clientY;
  };
  const handleTouchMove = (evt) => {
    if (!touchStartX || !touchStartY) {
      return;
    }
    const touch = evt.touches[0];
    const touchEndX = touch.clientX;
    const touchEndY = touch.clientY;
    const xDiff = touchStartX - touchEndX;
    const yDiff = touchEndY - touchEndY;

    if (Math.abs(xDiff) > Math.abs(yDiff)) {
      if (xDiff > 0) {
        onChangeImageIndex(hasNext ? activeImage + 1 : activeImage);
      } else {
        onChangeImageIndex(hasPrev ? activeImage - 1 : activeImage);
      }
    }
    touchStartX = null;
    touchStartY = null;
  };

  useEffect(() => {
    setActiveImage(mediaIndex);
  }, [mediaIndex]);

  useEffect(() => {
    if (imgDetailPosition && magnifierRef.current) {
      const getScaleDiff = () =>
        1 -
        (imgDetailPosition.originalImageWidth - imgRef.current.width) /
          imgDetailPosition.originalImageWidth;
      const diff = getScaleDiff();
      imgRef.current.parentNode.style.height = `${imgRef.current.height}px`;
      imgRef.current.parentNode.style.width = `${imgRef.current.width}px`;
      imgRef.current.parentNode.style.margin = "0 auto";

      magnifierRef.current.style.backgroundImage = `url('${items[activeImage].url}')`;
      magnifierRef.current.style.backgroundRepeat = "no-repeat";
      magnifierRef.current.style.backgroundSize = `${
        imgRef.current.width * imgDetailPosition.zoom
      }px ${imgRef.current.height * imgDetailPosition.zoom}px`;
      magnifierRef.current.style.backgroundPosition = `${
        imgDetailPosition.x * diff
      }px ${imgDetailPosition.y * diff}px`;
    }
  }, [imgDetailPosition]);

  const closeImageDetail = () => {
    if (room) {
      const { type, methods } = p2pMessageActions.userAction;
      imgRef.current.parentNode.style.height = "";
      imgRef.current.parentNode.style.width = "";
      imgRef.current.parentNode.style.margin = "";
      sendData({
        room: room.guest_id,
        type,
        method: methods.resetProductImageDetail,
      });
    }
  };

  return (
    <div className="vclerk-i-r-wrapper">
      {!!productId && (
        <>
          <div className="vclerk-i-r-arrow">
            {hasPrev && (
              <div
                aria-hidden
                role="button"
                className="vclerk-i-r-arrow-prev"
                onClick={() => onChangeImageIndex(activeImage - 1)}
              >
                <Arrow />
              </div>
            )}
          </div>
          <div className="vclerk-i-r-image-wrapper">
            <div
              className="vclerk-i-r-image"
              onTouchStart={handleTouchStart}
              onTouchMove={handleTouchMove}
            >
              <div
                ref={magnifierRef}
                className={`vclerk-magnifier ${
                  imgDetailPosition ? "vclerk-show-magnifier" : ""
                }`}
              />

              <div
                role="button"
                tabIndex={0}
                className={`vclerk-magnifier-close ${
                  imgDetailPosition ? "vclerk-show-close" : ""
                }`}
                onClick={() => closeImageDetail()}
              >
                <CloseIcon
                  width={20}
                  height={20}
                  color="rgba(255, 255, 255, 0.82)"
                />
              </div>
              <img
                ref={imgRef}
                srcSet={getSrcSetFromItem(items[activeImage])}
                sizes={imageSizes}
                src={items[activeImage].url_cdn || items[activeImage].url}
                alt={`item ${activeImage + 1}`}
              />
            </div>
            <div className="vclerk-i-r-dots-container">
              {items.length &&
                items.map((item, index) => (
                  <div
                    role="button"
                    tabIndex={0}
                    // eslint-disable-next-line react/no-array-index-key
                    key={`image-dot-${index}`}
                    onClick={() => onChangeImageIndex(index)}
                    className={`vclerk-i-r-dot ${
                      activeImage === index ? "vclerk-active-dot" : ""
                    }`}
                  />
                ))}
            </div>
          </div>

          <div className="vclerk-i-r-arrow">
            {hasNext && (
              <div
                aria-hidden
                role="button"
                className="vclerk-i-r-arrow-next vclerk-i-r-arrow"
                onClick={() => onChangeImageIndex(activeImage + 1)}
              >
                <Arrow />
              </div>
            )}
          </div>
        </>
      )}
    </div>
  );
};

ImageRotator.propTypes = {
  items: PropTypes.arrayOf(PropTypes.object).isRequired,
  productId: PropTypes.string.isRequired,
  mediaIndex: PropTypes.number.isRequired,
};

export default ImageRotator;
