import Skylink from "skylinkjs";
import p2pMessageActions from "./p2pMessageActions";

let skylinkInstance;

const setSkylinkKey = (skylinkKey) => {
  skylinkInstance = new Skylink({
    appKey: skylinkKey,
  });
};

function sendData({ room, type, method, data }) {
  skylinkInstance.sendP2PMessage(
    room,
    JSON.stringify({
      type,
      method,
      ...data,
    })
  );
}

export default () => {
  if (!skylinkInstance) {
    throw new Error("SkylinkJS: The API key has not been set;");
  }
  return skylinkInstance;
};

export { sendData, setSkylinkKey, p2pMessageActions };
